* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica Neue', Helvetica, sans-serif;
  line-height: 1.5;
  margin: 0;
  color: #111;
  background-color: #fff;
}

svg {
  max-height: 100%;
}

a {
  color: #07c;
}

h1,
h2,
h3 {
  font-weight: 600;
  line-height: 1.25;
  margin-top: 1em;
  margin-bottom: .5em;
}

h1 {
  font-size: 32px;
  font-size: 2rem;
}

h2 {
  font-size: 24px;
  font-size: 1.5rem;
}

h3 {
  font-size: 20px;
  font-size: 1.25rem;
}

p,
ul {
  margin-top: 1em;
  margin-bottom: 1em;
}

.h1 {
  font-size: 32px;
  font-size: 2rem;
}

.h3 {
  font-size: 20px;
  font-size: 1.25rem;
}

.h4 {
  font-size: 16px;
  font-size: 1rem;
}

.h6 {
  font-size: 12px;
  font-size: .75rem;
}

.text-decoration-none {
  text-decoration: none;
}

.bold {
  font-weight: bold;
  font-weight: bold;
}

.regular {
  font-weight: normal;
}

.caps {
  text-transform: uppercase;
  letter-spacing: .2em;
}

.center {
  text-align: center;
}

.list-reset {
  list-style: none;
  padding-left: 0;
}

.inline-block {
  display: inline-block;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.max-width-4 {
  max-width: 1024px;
  max-width: 64rem;
}

.m0 {
  margin: 0;
}

.my0 {
  margin-top: 0;
  margin-bottom: 0;
}

.ml1 {
  margin-left: 8px;
  margin-left: .5rem;
}

.my1 {
  margin-top: 8px;
  margin-top: .5rem;
  margin-bottom: 8px;
  margin-bottom: .5rem;
}

.my4 {
  margin-top: 64px;
  margin-top: 4rem;
  margin-bottom: 64px;
  margin-bottom: 4rem;
}

.mxn2 {
  margin-left: -16px;
  margin-left: -1rem;
  margin-right: -16px;
  margin-right: -1rem;
}

.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

.p1 {
  padding: 8px;
  padding: .5rem;
}

.px1 {
  padding-left: 8px;
  padding-left: .5rem;
  padding-right: 8px;
  padding-right: .5rem;
}

.p2 {
  padding: 16px;
  padding: 1rem;
}

.py2 {
  padding-top: 16px;
  padding-top: 1rem;
  padding-bottom: 16px;
  padding-bottom: 1rem;
}

.px2 {
  padding-left: 16px;
  padding-left: 1rem;
  padding-right: 16px;
  padding-right: 1rem;
}

.pb4 {
  padding-bottom: 64px;
  padding-bottom: 4rem;
}

.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.items-end {
  -webkit-box-align: end;
  -ms-flex-align: end;
  align-items: flex-end;
}

.justify-center {
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}

.justify-between {
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
}

.border {
  border-style: solid;
  border-width: 1px;
}

.border-top {
  border-top-style: solid;
  border-top-width: 1px;
}

.border-bottom {
  border-bottom-style: solid;
  border-bottom-width: 1px;
}

.h00 {
  font-size: 64px;
  font-size: 4rem;
}

.black {
  color: #111111;
}

.bg-cover {
  background-size: cover;
}

.bg-center {
  background-position: center;
}

body {
  font-family: 'Inconsolata', monospace;
}

header {
  background: url("../img/bg-1900.jpg");
}

header section {
  filter: url('data:image/svg+xml;charset=utf-8, <svg xmlns="http://www.w3.org/2000/svg"><filter id="filter"><feGaussianBlur stdDeviation="0" /><feComponentTransfer color-interpolation-filters="sRGB"><feFuncA type="table" tableValues="0 0.9" /></feComponentTransfer></filter></svg>#filter');
  -webkit-filter: drop-shadow(-1px 1px 0.6px slategrey) blur(0px) opacity(90%);
  filter: drop-shadow(-1px 1px 0.6px slategrey) blur(0px) opacity(90%);
}

header h1 {
  font-family: "Roboto Mono", sans-serif;
  font-weight: 700;
  line-height: .90;
  margin-top: .5em;
  margin-bottom: .25em;
}

header ul {
}

article p {
  font-family: "Roboto Mono", sans-serif;
  font-weight: 400;
  font-style: italic;
}

.caps-r {
  text-transform: uppercase;
}

.logo {
  fill: currentColor;
}

.logo:hover {
  fill: #3c464d;
  cursor: pointer;
}

i:hover {
  color: #3c464d;
}

@media (min-width: 40em) {
  .sm-col {
    float: left;
    box-sizing: border-box;
  }

  .sm-col-4 {
    width: 33.33333%;
  }

  .sm-col-6 {
    width: 50%;
  }

  .sm-col-8 {
    width: 66.66667%;
  }

  .sm-flex {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }

  .sm-mr3 {
    margin-right: 2rem;
  }

  .sm-ml3 {
    margin-left: 2rem;
  }

  .sm-mx3 {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .sm-my4 {
    margin-top: 4rem;
    margin-bottom: 4rem;
  }

  .sm-left-align {
    text-align: left;
  }

  .sm-subhead {
    margin-top: 3vw;
  }
}

@media (min-width: 52em) {
  .md-col-3 {
    width: 25%;
  }

  .md-col-9 {
    width: 75%;
  }

  .h00-responsive {
    font-size: 8vw;
  }

  .md-h5 {
    font-size: .875rem;
  }
}

@media (min-width: 96em) {
  .h00-responsive {
    font-size: 7.68rem;
  }
}

@media (min-width: 64em) {
  .lg-h4 {
    font-size: 1rem;
  }
}

@media (max-width: 40em) {
  .xs-logo {
    height: 2em !important;
  }

  .xs-header {
    text-align: left;
    margin-left: 0;
    font-family: "Roboto Mono", sans-serif;
  }

  .xs-subhead {
    text-align: left;
    margin-left: 0;
    line-height: 1.3;
  }
}
